.wrap-detail {
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}
.wrap-detail > div {
    width: 1000px;
}
.detail-top {
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 20px;
    margin-top: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid black;
}
.top-left {
    width: 35%;
}
.top-right {
    -ms-flex: 1 1;
        flex: 1 1;
}
.top-right .genre {
    margin-right: 10px
}
.detail-bottom {
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 20px;
    margin-left: 20px;
}
.detail-bottom img{
    width: 140px;
}
.card {
    -ms-flex: 1 1;
        flex: 1 1;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
            box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.card:hover {
    -webkit-box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
            box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.container {
    padding: 2px 16px;
}


